import { Box, Grid, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import React, { ReactElement } from 'react';
import { DialogContent, DialogTitle } from '../../shared/components/dialog';
import TextField from '@material-ui/core/TextField';
import { useStyles } from './styles/style';

interface Props {
    handleClose: () => void;
}

export default function ExportCard(props: Props): ReactElement {
    const classes = useStyles();
    const handleClose = () => {
        props.handleClose();
    };
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }
    return (
        <>
            <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={true}>
                <DialogTitle id="customized-dialog-title" onClose={handleClose}>
                    <Box component="h5" display="inline">
                        Export Recommendations
                    </Box>
                </DialogTitle>
                <DialogContent dividers>
                    <Box p={1}>
                        <h4>Export the Portfolio to My Portfolios</h4>
                        <TextField
                            style={{ width: '30rem' }}
                            id="outlined-search"
                            placeholder="Enter Name for the New Portfolio"
                            type="text"
                            variant="outlined"
                        />
                    </Box>
                </DialogContent>
                <DialogContent dividers>
                    <Box display={{ xs: 'none', sm: 'block' }}>
                        <Grid spacing={2} container direction="row" justify="space-around" alignItems="center">
                            <Grid item>
                                <Button
                                    className={classes.exportBtn}
                                    variant="contained"
                                    size={buttonSize()}
                                    color="primary"
                                    onClick={handleClose}
                                >
                                    Export
                                </Button>
                            </Grid>
                        </Grid>
                    </Box>
                </DialogContent>
            </Dialog>
        </>
    );
}
