import { Box, Divider, List, ListItem, ListItemText, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import ArrowForwardIosSharpIcon from '@material-ui/icons/ArrowForwardIosSharp';
import React, { ReactElement } from 'react';
import Select from 'react-select';
import { categories, funds } from './data/data';
import EvaluateFundGuide from './evaluateFundGuide';
import ShareFundCard from './shareFundCard';
import { useStyles } from './styles/style';
import ViewFundReport from './viewFundReport';

function EvaluateFund(): ReactElement {
    const classes = useStyles();
    // vfr - View Fund Report
    const [openvfr, setVFROpen] = React.useState(false);
    const [opensfc, setSFCOpen] = React.useState(false);
    const [openguide, setGuideOpen] = React.useState(false);
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }
    function handleVFRClose() {
        setVFROpen(false);
    }
    return (
        <React.Fragment>
            <main>
                <Box mt={5} mb={5}>
                    <Container>
                        <div className={classes.heroShaped}>
                            <h2 className="app-page-header">Evaluate a Fund</h2>
                            <Box mb={2}>
                                You can choose a mutual fund scheme from the following drop-down boxes to see its
                                detailed risk assessment. On selection of a category in the first dropdown box ,the
                                second dropdown box enables you to select a specific scheme in that category.
                            </Box>
                            <Box>
                                <Button
                                    variant="contained"
                                    size={buttonSize()}
                                    color="primary"
                                    onClick={() => {
                                        setGuideOpen(true);
                                    }}
                                >
                                    Guide
                                </Button>
                            </Box>
                        </div>
                    </Container>
                </Box>
                <Box pb={10} pt={5} className={classes.pageContent}>
                    <Box>
                        <Container>
                            <h3>Select Fund to Evaluate</h3>
                            <Grid container spacing={getSpacing()}>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Category: </Box>
                                    <Box>
                                        <Select
                                            className="basic-single"
                                            classNamePrefix="select"
                                            defaultValue={categories[0]}
                                            isClearable={true}
                                            isSearchable={true}
                                            name="label"
                                            options={categories}
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Fund: </Box>
                                    <Box>
                                        <Select
                                            defaultOptions
                                            className="basic-single"
                                            classNamePrefix="select"
                                            isClearable={true}
                                            isSearchable={true}
                                            name="color"
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                            </Grid>
                            <Grid container spacing={getSpacing()}>
                                <Grid item xs={12}>
                                    <Box mt={4} boxShadow={3}>
                                        <List component="nav" aria-labelledby="nested-list-subheader">
                                            <ListItem
                                                button
                                                onClick={() => {
                                                    setVFROpen(true);
                                                }}
                                            >
                                                <ListItemText primary="Purchased Funds" />
                                            </ListItem>
                                            <Divider />
                                            {funds.map((item, i) => (
                                                <React.Fragment key={i}>
                                                    <ListItem
                                                        button
                                                        onClick={() => {
                                                            setSFCOpen(true);
                                                        }}
                                                    >
                                                        <ListItemText secondary={item} />
                                                        <ArrowForwardIosSharpIcon fontSize="small" />
                                                    </ListItem>
                                                    {i < funds.length - 1 && <Divider />}
                                                </React.Fragment>
                                            ))}
                                        </List>
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                </Box>
                {openvfr && <ViewFundReport handleClose={handleVFRClose} />}
                {opensfc && (
                    <ShareFundCard
                        handleClose={() => {
                            setSFCOpen(false);
                        }}
                    />
                )}
                {openguide && (
                    <EvaluateFundGuide
                        handleClose={() => {
                            setGuideOpen(false);
                        }}
                    />
                )}
            </main>
        </React.Fragment>
    );
}

export default EvaluateFund;
