import React from 'react';
import { createStyles, Theme, withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import youtube from './../../assets/Group 5852@2x.png';
import Grid from '@material-ui/core/Grid';
import NativeSelect from '@material-ui/core/NativeSelect';
import { useVerticalTabStyles } from './styles/verticalTabStyles';
import { Styledtab } from './styles/tabStyles';
import { Styledtabs } from './styles/tabsStyles';
import { BootstrapInput } from './styles/dropdownStyles';

interface TabPanelProps {
    children?: React.ReactNode;
    index: any;
    value: any;
}

function TabPanel(props: TabPanelProps) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`vertical-tabpanel-${index}`}
            aria-labelledby={`vertical-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={{ xs: 0, sm: 3 }}>
                    <Typography component="div">{children}</Typography>
                </Box>
            )}
        </div>
    );
}

function a11yProps(index: any) {
    return {
        id: `vertical-tab-${index}`,
        'aria-controls': `vertical-tabpanel-${index}`,
    };
}

export default function VerticalTabs() {
    const classes = useVerticalTabStyles();
    const [value, setValue] = React.useState(0);

    // eslint-disable-next-line @typescript-eslint/ban-types
    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    const handleSelectChange = (event: React.ChangeEvent<{ value: unknown }>) => {
        setValue(parseInt(event.target.value as string));
    };

    return (
        <Grid container spacing={3}>
            <Grid item xs={12} sm={3}>
                <Box className={classes.menuDropdownContainer} mt={3} display={{ xs: 'block', sm: 'none' }}>
                    <NativeSelect
                        id="demo-customized-select-native"
                        className={classes.menuDropdown}
                        value={value}
                        onChange={handleSelectChange}
                        input={<BootstrapInput />}
                    >
                        <option value={0}>Credit Risk</option>
                        <option value={1}>Duration Risk</option>
                        <option value={2}>Liquidity Risk</option>
                        <option value={3}>Past performance vs Future Prospects</option>
                    </NativeSelect>
                </Box>
                <Box mt={3} display={{ xs: 'none', sm: 'block' }}>
                    <Styledtabs
                        orientation="vertical"
                        variant="fullWidth"
                        value={value}
                        onChange={handleChange}
                        aria-label="Vertical tabs example"
                        centered={false}
                        scrollButtons="off"
                    >
                        <Styledtab label="Credit Risk" {...a11yProps(0)} />
                        <Styledtab label="Duration Risk" {...a11yProps(1)} />
                        <Styledtab label="Liquidity Risk" {...a11yProps(2)} />
                        <Styledtab label="Past performance vs Future Prospects" {...a11yProps(3)} />
                    </Styledtabs>
                </Box>
            </Grid>
            <Grid item sm={9} xs={12}>
                <TabPanel value={value} index={0}>
                    <Box mb={2} className={classes.contentHeading}>
                        Credit Risk
                    </Box>
                    <Typography component="div">
                        <Box className={classes.content} mb={3}>
                            It refers to the risk that a lender (investor) actually, or in perception may not receive
                            the owed principal and/or interest from an issuer in a timely manner, which results in an
                            interruption of cash flows and increased costs for collection.
                        </Box>
                        <img src={youtube} alt="logo" style={{ width: '100%' }} />
                        <Box className={classes.content} mt={3} mb={3}>
                            Even though the issuer might not have defaulted, in the perception of rating agencies, if
                            the probability of default increases, the agencies will lower the credit rating of the
                            issuer or assign a negative outlook. The perception changes based on macro, micro
                            (industry), and issuer level deterioration in business conditions and financials.
                        </Box>

                        <Box className={classes.content} mt={3} mb={1}>
                            Looking at a typical sequence of events:
                        </Box>

                        <Box>
                            <ol className={classes.orderedList}>
                                <li className={classes.liContent}>
                                    As soon as either the perception of rating agencies change, or the issuer does not
                                    pay the interest or principal due.
                                </li>
                                <li className={classes.liContent}>
                                    The issuer is downgraded in terms of credit rating and communicated to the public.
                                </li>
                                <li className={classes.liContent}>
                                    The very same day, the valuation agency will move it to a higher interest rate
                                    (yield) curve, as investors will demand a higher interest rate to buy the same
                                    issuers bonds, because of increased risk.
                                </li>
                                <li className={classes.liContent}>
                                    The increased interest (yield) will lead to a capital loss and the NAV (Net Asset
                                    Value) of the fund falls proportional to the issuers percentage exposure in the
                                    portfolio.
                                </li>
                                <li className={classes.liContent}>Investors in the fund experience a loss.</li>
                            </ol>
                        </Box>
                    </Typography>
                </TabPanel>
                <TabPanel value={value} index={1}>
                    Duration Risk
                </TabPanel>
                <TabPanel value={value} index={2}>
                    Liquidity Risk
                </TabPanel>
                <TabPanel value={value} index={3}>
                    Past performance vs Future Prospects
                </TabPanel>
            </Grid>
        </Grid>
    );
}
