import React, { ReactElement, useContext, useEffect } from 'react';
import clsx from 'clsx';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import { NavLink, useHistory } from 'react-router-dom';
import AppBar from '@material-ui/core/AppBar';
import { manuBarStyles } from './styles/topMenuBar';
import { Box, Container, Toolbar } from '@material-ui/core';
import RouterDomLink from '../components/routerDomLink';
import SortIcon from '@material-ui/icons/Sort';
import logo from './../../assets/logo/logo.png';
import UserInfo from './../../assets/UserInfo.png';
import { responsivemenustyles } from './styles/responsivemenu';
import { AuthContext } from '../config/authcontextprovider';

type Anchor = 'left';
interface ResponsiveMenuItem {
    name: string;
    url: string;
}

interface MenuProps {
    hideMenu: () => void;
    showMenu: boolean;
}
export default function ResponsiveMenu(props: MenuProps): ReactElement {
    const anchor = 'left';
    const menus = [
        { name: 'Evaluate a Fund', url: 'EvaluateFund' },
        { name: 'Portfolio Deep Dive', url: 'portfolio' },
        { name: 'Compare Funds', url: '' },
        { name: 'My Fund Portfolios', url: 'myfundPortfolios' },
        { name: 'Good Funds for me', url: 'goodFunds' },
        { name: 'Knowledge Center', url: 'knowledgecenter' },
        { name: 'Buy a Plan', url: 'BuyAPlan' },
    ] as ResponsiveMenuItem[];

    const history = useHistory();
    const navigate = (item: ResponsiveMenuItem) => {
        history.push(item.url);
    };
    // const classes = useStyles();
    const classes = responsivemenustyles();
    const [state, setState] = React.useState(props.showMenu);

    useEffect(() => {
        setState(props.showMenu); //children function of interest
    }, [props.showMenu]);
    const { authenticated, setAuth } = useContext(AuthContext);
    const toggleDrawer = (anchor: Anchor, open: boolean) => (event: React.KeyboardEvent | React.MouseEvent) => {
        if (
            event.type === 'keydown' &&
            ((event as React.KeyboardEvent).key === 'Tab' || (event as React.KeyboardEvent).key === 'Shift')
        ) {
            return;
        }

        setState(open);
        props.hideMenu();
    };

    const logout = () => {
        setState(false);
        props.hideMenu();
        setAuth(false);
    };
    const list = (anchor: Anchor) => (
        <div
            className={clsx(classes.list)}
            role="presentation"
            onClick={toggleDrawer(anchor, false)}
            onKeyDown={toggleDrawer(anchor, false)}
        >
            <List>
                {menus.map((item, index) => (
                    <ListItem
                        button
                        key={index}
                        component={NavLink}
                        exact
                        to={item.url}
                        activeClassName={classes.listItemActive}
                        className={classes.listItem}
                    >
                        {item.name}
                    </ListItem>
                ))}
            </List>
        </div>
    );

    return (
        <Drawer
            anchor={anchor}
            open={state}
            onClose={toggleDrawer(anchor, false)}
            classes={{ paper: classes.appdrawerwrap }}
        >
            <AppBar position="relative" color="transparent" elevation={0}>
                <Toolbar className={classes.menuWrapper}>
                    <Container>
                        <Box display="flex" alignItems="center" width="95%" ml={'auto'} mr={'auto'}>
                            <Box className={classes.hamburgerMenuWrap} onClick={toggleDrawer(anchor, false)}>
                                <SortIcon className={classes.hamburgerMenu} />
                            </Box>
                            <Box className={classes.title} pt={2} pb={2}>
                                <RouterDomLink to="/" replace>
                                    <img src={logo} alt="logo" />
                                </RouterDomLink>
                            </Box>
                        </Box>
                    </Container>
                </Toolbar>
                <Box className={classes.headerBorder} display={{ xs: 'block', sm: 'none' }}></Box>
            </AppBar>
            <Container>
                <Box display="flex" alignItems="center">
                    <Box mr={2} mt={2}>
                        <img src={UserInfo} alt="logo" />
                    </Box>
                    <Box component="h3">Denise Powell</Box>
                </Box>
                <Box mt={2}>{list(anchor)}</Box>
                <Box className={classes.bottomAction}>
                    <Box mb={4}>
                        <Box component="div" className={classes.listItem} onClick={logout}>
                            Logout
                        </Box>
                    </Box>
                    <Box> 2020 FundSage. All Rights Reserved.</Box>
                </Box>
            </Container>
        </Drawer>
    );
}
