import React, { ReactElement } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { fundportfoliosdata } from './data/data';
import DeleteIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

export default function FundPortfolioTable(): ReactElement {
    const classes = useStyles();

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Fund Name</TableCell>
                        <TableCell>Net YTM(%)</TableCell>
                        <TableCell>Rtr. Risk Ratio</TableCell>
                        <TableCell>Credit Risk</TableCell>
                        <TableCell>Int. Rate Risk</TableCell>
                        <TableCell>Liquidity Risk</TableCell>
                        <TableCell>Total Risk Score</TableCell>
                        <TableCell>Weight/Value</TableCell>
                        <TableCell>Percentage(%)</TableCell>
                        <TableCell></TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {fundportfoliosdata.map((row, i) => (
                        <TableRow key={i}>
                            <TableCell component="th" scope="row">
                                {row.name}
                            </TableCell>
                            <TableCell>{row.net}</TableCell>
                            <TableCell>{row.riskratio}</TableCell>
                            <TableCell style={{ color: row.creditrisk.color }}>{row.creditrisk.value}</TableCell>
                            <TableCell style={{ color: row.raterisk.color }}>{row.raterisk.value}</TableCell>
                            <TableCell style={{ color: row.liquidityrisk.color }}>{row.liquidityrisk.value}</TableCell>
                            <TableCell style={{ color: row.totalrisk.color }}>{row.totalrisk.value}</TableCell>
                            <TableCell>{row.weight}</TableCell>
                            <TableCell>{row.percentage}</TableCell>
                            <TableCell>
                                <DeleteIcon fontSize="small" />
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
