import React, { ReactElement } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});

function createData(name: string, calories: number, fat: number, carbs: number, protein: number, color: string) {
    return { name, calories, fat, carbs, protein, color };
}

const rows = [
    createData('State Govt. in India', 0.4, 6, 0.4, 4.0, '#059C00'),
    createData('Bank of Baroda', 0.8, 9, 0.8, 4.3, '#059C00'),
    createData('Govt. of India', 11.3, 16, 11.3, 6.0, '#FF9100'),
    createData('Export - Import Bank of India', 1.8, 3.7, 1.8, 4.3, '#059C00'),
    createData('Grasim Industries Ltd.', 0, 16, 0, 3.9, '#059C00'),
    createData('icici Bank Ltd.', 1.6, 9, 1.6, 3.9, '#FF9100'),
    createData('Indian Oil Corp. Ltd.', 2.3, 16, 2.3, 3.9, '#059C00'),
    createData('National Housing Bank', 1.9, 3.7, 1.9, 3.9, '#059C00'),
    createData('NHPC Ltd.', 4, 16, 4, 3.9, '#FF0000'),
    createData('NHPC Ltd.', 7.1, 9, 7.1, 3.9, '#059C00'),
];

export default function PortfolioTable(): ReactElement {
    const classes = useStyles();

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell>Borrower Name</TableCell>
                        <TableCell>Allocation(%)</TableCell>
                        <TableCell>Avg MF Holding (%)</TableCell>
                        <TableCell>Risk Score</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row, i) => (
                        <TableRow key={i}>
                            <TableCell component="th" scope="row">
                                {row.name}
                            </TableCell>
                            <TableCell>{row.calories}</TableCell>
                            <TableCell>{row.fat}</TableCell>
                            <TableCell style={{ color: row.color }}>{row.carbs}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}
