import { Box, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import React, { ReactElement } from 'react';
import Select from 'react-select';
import { categories } from './data/data';
import PortfolioTable from './portfolioTable';
import { useStyles } from './styles/style';

function Portfolio(): ReactElement {
    const classes = useStyles();
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }

    return (
        <React.Fragment>
            <main>
                <Box mt={5} mb={5}>
                    <Container>
                        <div className={classes.heroShaped}>
                            <h2 className="app-page-header">Portfolio Deep Dive</h2>
                            <Box mb={2}>
                                To study companies in the portfolio of mutual fund scheme, select a scheme frombelow
                                dropdown boxes to display the full portfolio of the scheme.
                            </Box>
                            <Box>
                                <Button variant="contained" size={buttonSize()} color="primary">
                                    Guide
                                </Button>
                            </Box>
                        </div>
                    </Container>
                </Box>
                <Box pb={10} pt={5} className={classes.pageContent}>
                    <Box>
                        <Container>
                            <h3>Select Fund to Evaluate</h3>
                            <Grid container spacing={getSpacing()}>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Category: </Box>
                                    <Box>
                                        <Select
                                            className="basic-single"
                                            classNamePrefix="select"
                                            defaultValue={categories[0]}
                                            isClearable={true}
                                            isSearchable={true}
                                            name="label"
                                            options={categories}
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Fund: </Box>
                                    <Box>
                                        <Select
                                            defaultOptions
                                            className="basic-single"
                                            classNamePrefix="select"
                                            isClearable={true}
                                            isSearchable={true}
                                            name="color"
                                        />
                                    </Box>
                                </Grid>
                            </Grid>
                            <Grid container>
                                <Grid item xs={12}>
                                    <Box mt={4}>
                                        <PortfolioTable />
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                    <Box pt={5}>
                        <Container>
                            <Grid container justify="center" spacing={getSpacing()}>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Show Report
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Export to CSV
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Print
                                    </Button>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                </Box>
            </main>
        </React.Fragment>
    );
}

export default Portfolio;
