import { Box, TextField } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import clsx from 'clsx';
import React, { ReactElement, useContext } from 'react';
import { GoogleLogin } from 'react-google-login';
import { useHistory } from 'react-router-dom';
import { Divider } from '../../shared/components/divider';
import { AuthContext } from '../../shared/config/authcontextprovider';
import { useStyles } from './styles/style';

interface Props {
    toggle: () => void;
}

export function LoginForm(props: Props): ReactElement {
    const classes = useStyles();
    const history = useHistory();
    function onToggle() {
        props.toggle();
    }
    const { authenticated, setAuth } = useContext(AuthContext);
    function login() {
        setAuth(true);
        history.push('/');
    }
    const responseGoogle = (response: any) => {
        console.log(response);
    };
    return (
        <React.Fragment>
            <Box className={clsx(classes.heroShapedWrapperFirstConent)} ml={3} mr={3}>
                <Box className={classes.headerText} mb={2} display="flex" justifyContent="center">
                    <Box className={classes.appTextHighlight} display="inline" mr={1}>
                        Login
                    </Box>
                    to your Account
                </Box>
                <Box display="flex" justifyContent="center" mb={3} mt={3}>
                    <GoogleLogin
                        clientId="658977310896-knrl3gka66fldh83dao2rhgbblmd4un9.apps.googleusercontent.com"
                        buttonText="Login/Register with google"
                        onSuccess={responseGoogle}
                        onFailure={responseGoogle}
                        cookiePolicy={'single_host_origin'}
                    />
                    {/* <Button type="button" variant="contained" color="default" className={classes.googlebtn}>
                        Login/Register with google
                    </Button> */}
                </Box>
                <Box>
                    <Grid container component="main">
                        <Grid item>
                            <Box className={classes.paper} display="flex" justifyContent="center">
                                <form className={classes.form} noValidate>
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="email"
                                        label="Email"
                                        name="email"
                                        autoComplete="email"
                                        autoFocus
                                        size="small"
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        name="password"
                                        label="Password"
                                        type="password"
                                        id="password"
                                        autoComplete="current-password"
                                        size="small"
                                    />
                                    <Box display="flex" justifyContent="center" mb={3} mt={3}>
                                        <Button
                                            type="button"
                                            variant="contained"
                                            color="primary"
                                            className={classes.submit}
                                            onClick={login}
                                        >
                                            Login
                                        </Button>
                                    </Box>
                                    <Box>
                                        <Divider>Or</Divider>
                                    </Box>
                                    <Box
                                        display="flex"
                                        className={classes.registerbtn}
                                        justifyContent="center"
                                        mb={3}
                                        mt={3}
                                        onClick={onToggle}
                                    >
                                        Register
                                    </Box>
                                </form>
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
        </React.Fragment>
    );
}
