import { Box, Container, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import React, { ReactElement, useState } from 'react';
import { Switch, Route, Redirect, useLocation } from 'react-router-dom';
import './App.css';
import bgImage from './assets/Group17.svg';
import landingBg from './assets/landing/LandingBg@2x.png';
import CompareFunds from './features/compareFunds/compareFunds';
import EvaluateFund from './features/evaluateFund/evaluateFund';
import GoodFunds from './features/goodFunds/goodFunds';
import FundPortfolios from './features/fundPortfolios/fundPortfolios';
import Home from './features/home/home';
import KnowledgeCenter from './features/knowledge_center/knowledgeCenter';
import Landing from './features/landing/landing';
import Portfolio from './features/portfolio/portfolio';
import Pricing from './features/pricing/pricing';
import ProtectedRoute, { ProtectedRouteProps } from './protectedRoute';
import PublicRoute, { PublicRouteProps } from './publicRoute';
import { AuthContext } from './shared/config/authcontextprovider';
import Footer from './shared/layout/footer';
import { MainContainer } from './shared/layout/mainContainer';
import TopMenuBar from './shared/layout/topMenuBar';

function App(): ReactElement {
    const location = useLocation();
    let style = {};
    if (location.pathname == '/') {
        style = { backgroundImage: `url(${bgImage})` };
    } else if (location.pathname == '/landing') {
        style = { backgroundImage: `url(${landingBg})`, backgroundRepeat: 'no-repeat', backgroundSize: '100% 100vh' };
    } else {
        style = { backgroundColor: `#F7FBFF` };
    }
    const setAuth = (authenticated: boolean) => {
        setState({ ...state, authenticated: authenticated });
    };

    const initState = {
        authenticated: false,
        setAuth: setAuth,
    };

    const [state, setState] = useState(initState);
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));

    const defaultProtectedRouteProps: ProtectedRouteProps = {
        isAuthenticated: state.authenticated,
        authenticationPath: '/landing',
        redirectPathOnAuthentication: '/',
    };
    const defaultPublicRouteProps: PublicRouteProps = {
        isAuthenticated: state.authenticated,
        redirectionPath: '/',
    };

    return (
        <React.Fragment>
            <AuthContext.Provider value={state}>
                <div style={style}>
                    <CssBaseline />
                    <TopMenuBar />
                    {/* {!state.authenticated && isSmallScreen && (
                        <Box mt={3} display={{ xs: 'block', sm: 'none' }}>
                            <Container>
                                <Grid container spacing={2} justify="center">
                                    <Grid item>
                                        <Button variant="contained" size="large" color="primary">
                                            Register
                                        </Button>
                                    </Grid>
                                    <Grid item>
                                        <Button
                                            variant="outlined"
                                            size="large"
                                            color="primary"
                                            onClick={() => state.setAuth(true)}
                                        >
                                            Login
                                        </Button>
                                    </Grid>
                                </Grid>
                            </Container>
                        </Box>
                    )} */}

                    <MainContainer>
                        <Switch>
                            <ProtectedRoute {...defaultProtectedRouteProps} exact={true} path="/" component={Home} />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/portfolio"
                                component={Portfolio}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/EvaluateFund"
                                component={EvaluateFund}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/knowledgecenter"
                                component={KnowledgeCenter}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/compareFunds"
                                component={CompareFunds}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/goodFunds"
                                component={GoodFunds}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/BuyAPlan"
                                component={Pricing}
                            />
                            <ProtectedRoute
                                {...defaultProtectedRouteProps}
                                exact={true}
                                path="/myfundPortfolios"
                                component={FundPortfolios}
                            />

                            {/* <Route exact path="/landing" render={() => <Landing />}></Route> */}
                            {/* <Route exact path="/portfolio" render={() => <Portfolio />}></Route> */}
                            {/* <Redirect exact from="/" to="/landing" /> */}

                            <PublicRoute {...defaultPublicRouteProps} exact path="/landing" component={Landing} />
                            {/* <Redirect exact from="/" to="/home" /> */}
                            <Route render={() => <Redirect to="/" />} />
                            {/* <Route exact path="/landing" render={() => <Landing />}></Route> */}
                        </Switch>
                    </MainContainer>
                    <Footer />
                </div>
            </AuthContext.Provider>
        </React.Fragment>
    );
}

export default App;
