import AppBar from '@material-ui/core/AppBar';
import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Toolbar from '@material-ui/core/Toolbar';
import React, { ReactElement, useContext } from 'react';
import RouterDomLink from '../components/routerDomLink';
import logo from './../../assets/logo/logo.png';
import { manuBarStyles } from './styles/topMenuBar';
import TopMenuButton from './topMenuButton';
import SortIcon from '@material-ui/icons/Sort';
import userImage from './../../assets/Group5899@2x.png';
import userImageXs from './../../assets/Group5899.png';
import { Container, Grid } from '@material-ui/core';
import { AuthContext } from '../config/authcontextprovider';
import ResponsiveMenu from './responsivemenu';

function TopMenuBar(): ReactElement {
    const classes = manuBarStyles();
    const [open, setOpen] = React.useState(false);
    const [opensidemenu, setSideMenuOpen] = React.useState(false);
    const anchorRef = React.useRef<HTMLButtonElement>(null);

    // return focus to the button when we transitioned from !open -> open
    const prevOpen = React.useRef(open);
    React.useEffect(() => {
        if (prevOpen.current === true && open === false) {
            anchorRef.current!.focus();
        }

        prevOpen.current = open;
    }, [open]);
    const toggleSideMenu = (toOpen: boolean) => {
        setSideMenuOpen(toOpen);
    };
    const { authenticated, setAuth } = useContext(AuthContext);
    return (
        <AppBar position="relative" color="transparent" elevation={0}>
            {authenticated && (
                <Container>
                    <Toolbar className={classes.menuWrapper}>
                        <Box
                            className={classes.hamburgerMenuWrap}
                            onClick={() => {
                                toggleSideMenu(true);
                            }}
                        >
                            <SortIcon className={classes.hamburgerMenu} />
                        </Box>
                        <Box className={classes.title} pt={2} pb={2}>
                            <RouterDomLink to="/" replace>
                                <img src={logo} alt="logo" />
                            </RouterDomLink>
                        </Box>
                        {authenticated && (
                            <Box
                                display={{ xs: 'block', md: 'none' }}
                                onClick={() => setAuth(false)}
                                className={classes.xsUserIconWrap}
                            >
                                <img src={userImageXs} />
                            </Box>
                        )}
                        <Box display={{ xs: 'none', md: 'block' }}>
                            <Box display="flex" flexDirection="row" p={1} m={1} className={classes.menuItemWrapper}>
                                {authenticated && (
                                    <>
                                        <Box className={classes.menuItem}>
                                            <TopMenuButton
                                                title="Deep Dive"
                                                menuList={[
                                                    { name: 'Evaluate Fund', url: '/EvaluateFund' },
                                                    { name: 'Portfolio Deep Dive', url: '/portfolio' },
                                                ]}
                                            />
                                        </Box>
                                        <Box className={classes.menuItem}>
                                            <Button>
                                                <RouterDomLink to="compareFunds" replace>
                                                    Compare Funds
                                                </RouterDomLink>
                                            </Button>
                                        </Box>
                                        <Box className={classes.menuItem}>
                                            <TopMenuButton
                                                title="My Funds"
                                                menuList={[
                                                    { name: 'My Fund Portfolio', url: '/myfundPortfolios' },
                                                    { name: 'My Good Fund for Me', url: '/goodFunds' },
                                                    { name: 'Buy a Plan', url: '/BuyAPlan' },
                                                ]}
                                            />
                                        </Box>
                                        <Box className={classes.menuItem}>
                                            <Button>Intel</Button>
                                        </Box>
                                        <Box className={classes.menuItem}>
                                            <Button>
                                                <RouterDomLink to="knowledgecenter" replace>
                                                    Knowledge Center
                                                </RouterDomLink>
                                            </Button>
                                        </Box>
                                    </>
                                )}
                                {authenticated && (
                                    <Box className={classes.menuItem} onClick={() => setAuth(false)}>
                                        <img src={userImageXs} />
                                    </Box>
                                )}
                            </Box>
                        </Box>
                    </Toolbar>

                    <Box className={classes.headerBorder} display={{ xs: 'block', sm: 'none' }}></Box>

                    <ResponsiveMenu
                        hideMenu={() => {
                            toggleSideMenu(false);
                        }}
                        showMenu={opensidemenu}
                    />
                </Container>
            )}
            {!authenticated && (
                <>
                    <Grid container>
                        <Grid item xs={12} sm={8}>
                            <Box pt={2} pb={2} className={classes.loggoutAppBar}>
                                <Container className={classes.loggoutAppBarContainer}>
                                    <RouterDomLink to="/" replace>
                                        <img src={logo} alt="logo" />
                                    </RouterDomLink>
                                </Container>
                            </Box>
                        </Grid>
                        <Grid item xs={12} sm={4} className={classes.loginBar}></Grid>
                    </Grid>
                    <Box className={classes.headerBorder} display={{ xs: 'block', sm: 'none' }}></Box>
                </>
            )}
        </AppBar>
    );
}

export default TopMenuBar;
