import { Divider, List, ListItem, ListItemText } from '@material-ui/core';
import React, { ReactElement } from 'react';
import { browserProfile } from './data/data';

export default function FundList(): ReactElement {
    return (
        <>
            {browserProfile.map((item, i) => (
                <React.Fragment key={i}>
                    <ListItem button>
                        <ListItemText secondary={item.name} />
                        {item.value}
                    </ListItem>
                    {i < browserProfile.length - 1 && <Divider />}
                </React.Fragment>
            ))}
        </>
    );
}
