import { Box, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import React, { ReactElement } from 'react';
import Select from 'react-select';
import { categories } from './data/data';
import CompareFundsTable from './compareFundsTable';
import { useStyles } from './styles/style';

function CompareFunds(): ReactElement {
    const classes = useStyles();
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }

    return (
        <React.Fragment>
            <main>
                <Box mt={5} mb={5}>
                    <Container>
                        <div className={classes.heroShaped}>
                            <h2 className="app-page-header">Compare Funds</h2>
                            <Box mb={2} className={classes.lightText}>
                                You can compare funds in a single category by simply selecting that category from the
                                first dropdown box (this does not require any additional selection in the second
                                dropdown).
                            </Box>
                            <Box>
                                <Button variant="contained" size={buttonSize()} color="primary">
                                    Guide
                                </Button>
                            </Box>
                        </div>
                    </Container>
                </Box>
                <Box pb={10} pt={5} className={classes.pageContent}>
                    <Box>
                        <Container>
                            <Grid container spacing={getSpacing()}>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Select a category of funds:</Box>
                                    <Box>
                                        <Select
                                            className="basic-single"
                                            classNamePrefix="select"
                                            defaultValue={categories[0]}
                                            isClearable={true}
                                            isSearchable={true}
                                            name="label"
                                            options={categories}
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Select specific fund: </Box>
                                    <Box>
                                        <Select
                                            defaultOptions
                                            className="basic-single"
                                            classNamePrefix="select"
                                            isClearable={true}
                                            isSearchable={true}
                                            name="color"
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                            </Grid>
                            <Grid container>
                                <Grid item xs={12}>
                                    <Box mt={4}>
                                        <CompareFundsTable />
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                    <Box pt={5}>
                        <Container>
                            <Grid container justify="center" spacing={getSpacing()}>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Export to CSV
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Reset
                                    </Button>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                    <Box>
                        <Container>
                            <Grid container justify="center" spacing={getSpacing()}>
                                <Grid item xs={12}>
                                    <Box p={3} className={classes.note}>
                                        YTM is actual YTM - Expense ratio <br />
                                        <br />
                                        Total Risk Quality Score represents a weighted average of credit, interest rate
                                        and liquidity risk, also adjusted for concentration; higher score means lesser
                                        risk Return to Risk ratio is calculated from the fund&apos;s YTM, past returns
                                        and total risk. Higer ratio means more returns for the risk.
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                </Box>
            </main>
        </React.Fragment>
    );
}

export default CompareFunds;
