import { Box, Grid, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import React, { ReactElement } from 'react';
import { DialogActions, DialogContent, DialogTitle } from '../../shared/components/dialog';
import vfrImage from './../../assets/ViewFundReport/Group5867.png';

interface Props {
    handleClose: () => void;
}

export default function ViewFundReport(props: Props): ReactElement {
    const handleClose = () => {
        props.handleClose();
    };
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }
    return (
        <>
            <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={true}>
                <DialogTitle id="customized-dialog-title" onClose={handleClose}>
                    <Box component="h5" display="inline">
                        View Fund Report
                    </Box>
                </DialogTitle>
                <DialogContent dividers>
                    <p>
                        Your current subscription doesnt include detailed fund reports beyond what you have already
                        taken.
                    </p>
                    <p>
                        Below is a quick snapshot of our assessment of this fund. You can purchase a single fund report
                        or a plan for multiple funds to see the detailed report.
                    </p>
                    <p>
                        <img src={vfrImage} alt="View Fund Report" style={{ width: '100%' }} />
                    </p>
                    <p>
                        You can also share the above snapshot on your social media to see this report and get 9 more
                        credits for detailed fund reports.
                    </p>
                </DialogContent>
                <DialogActions>
                    <Grid spacing={2} container direction="row" justify="space-around" alignItems="center">
                        <Grid item>
                            <Button variant="contained" size={buttonSize()} color="primary" onClick={handleClose}>
                                Share a Fund
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button variant="contained" size={buttonSize()} color="primary" onClick={handleClose}>
                                Buy Single report
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button variant="contained" size={buttonSize()} color="primary" onClick={handleClose}>
                                Buy a Plan
                            </Button>
                        </Grid>
                    </Grid>
                </DialogActions>
            </Dialog>
        </>
    );
}
