import { Box, IconButton, InputAdornment, TextField } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Visibility, VisibilityOff } from '@material-ui/icons';
import React, { ReactElement, useState } from 'react';
import { Divider } from '../../shared/components/divider';
import { useStyles } from './styles/style';

interface Props {
    toggle: () => void;
}

export function RegisterForm(props: Props): ReactElement {
    const classes = useStyles();
    const [showPassword, setShowPassword] = useState(false);
    const handleClickShowPassword = () => setShowPassword(!showPassword);
    const handleMouseDownPassword = () => setShowPassword(!showPassword);
    return (
        <React.Fragment>
            <Box className={classes.heroShapedWrapperFirstConent} ml={3} mr={3}>
                <Box className={classes.headerText} mb={2} display="flex" justifyContent="center">
                    <Box className={classes.appTextHighlight} display="inline" mr={1}>
                        Register
                    </Box>
                    your Account
                </Box>

                <Box>
                    <Grid container component="main">
                        <Grid item>
                            <Box className={classes.paper} display="flex" justifyContent="center">
                                <form className={classes.form} noValidate>
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="Username"
                                        label="Username"
                                        name="Username"
                                        autoComplete="Username"
                                        autoFocus
                                        size="small"
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        id="email"
                                        label="Email"
                                        name="email"
                                        autoComplete="email"
                                        size="small"
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        name="password"
                                        label="Password"
                                        type={showPassword ? 'text' : 'password'}
                                        id="password"
                                        autoComplete="current-password"
                                        size="small"
                                        InputProps={{
                                            // <-- This is where the toggle button is added.
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                    <IconButton
                                                        aria-label="Toggle password visibility"
                                                        onClick={handleClickShowPassword}
                                                        onMouseDown={handleMouseDownPassword}
                                                    >
                                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                                    </IconButton>
                                                </InputAdornment>
                                            ),
                                        }}
                                    />
                                    <TextField
                                        variant="outlined"
                                        margin="normal"
                                        required
                                        fullWidth
                                        name="ConfirmPassword"
                                        label="Confirm Password"
                                        type={showPassword ? 'text' : 'password'}
                                        id="ConfirmPassword"
                                        autoComplete="current-password"
                                        size="small"
                                        InputProps={{
                                            // <-- This is where the toggle button is added.
                                            endAdornment: (
                                                <InputAdornment position="end">
                                                    <IconButton
                                                        aria-label="Toggle password visibility"
                                                        onClick={handleClickShowPassword}
                                                        onMouseDown={handleMouseDownPassword}
                                                    >
                                                        {showPassword ? <Visibility /> : <VisibilityOff />}
                                                    </IconButton>
                                                </InputAdornment>
                                            ),
                                        }}
                                    />
                                    <Box display="flex" justifyContent="center" mb={3} mt={3}>
                                        <Button
                                            type="button"
                                            variant="contained"
                                            color="primary"
                                            className={classes.submit}
                                        >
                                            Register
                                        </Button>
                                    </Box>
                                    <Box>
                                        <Divider>Or</Divider>
                                    </Box>
                                    <Box
                                        display="flex"
                                        className={classes.registerbtn}
                                        justifyContent="center"
                                        mb={3}
                                        mt={3}
                                        onClick={props.toggle}
                                    >
                                        Back to login
                                    </Box>
                                </form>
                            </Box>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
        </React.Fragment>
    );
}
