import { Box, useMediaQuery } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import React, { ReactElement } from 'react';
import Select from 'react-select';
import { categories, options } from './data/data';
import GoodFundsTable from './goodFundsTable';
import ExportCard from './exportCard';
import { useStyles } from './styles/style';

function GoodFunds(): ReactElement {
    const classes = useStyles();
    const [openExport, setExportOpen] = React.useState(false);
    const isSmallScreen = useMediaQuery((theme: any) => theme.breakpoints.down('xs'));
    function buttonSize() {
        return isSmallScreen ? 'large' : 'medium';
    }
    function getSpacing() {
        return isSmallScreen ? 1 : 9;
    }

    return (
        <React.Fragment>
            <main>
                <Box mt={5} mb={5}>
                    <Container>
                        <div className={classes.heroShaped}>
                            <h2 className="app-page-header">Good Funds for me</h2>
                            <Box mb={2} className={classes.lightText}>
                                Debt mutual funds suitable to you depend on your objective, investment horizon and such.
                                You can select the objective in the first dropdown and the applicable detail in the
                                second dropdown.
                            </Box>
                            <Box>
                                <Button variant="contained" size={buttonSize()} color="primary">
                                    Guide
                                </Button>
                            </Box>
                        </div>
                    </Container>
                </Box>
                <Box pb={10} pt={5} className={classes.pageContent}>
                    <Box>
                        <Container>
                            <Grid container spacing={getSpacing()}>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Purpose:</Box>
                                    <Box>
                                        <Select
                                            className="basic-single"
                                            classNamePrefix="select"
                                            defaultValue={categories[0]}
                                            isClearable={true}
                                            isSearchable={true}
                                            name="label"
                                            options={categories}
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                                <Grid item sm={6} xs={12}>
                                    <Box mb={2}>Options: </Box>
                                    <Box>
                                        <Select
                                            defaultOptions
                                            defaultValue={options[0]}
                                            className="basic-single"
                                            classNamePrefix="select"
                                            isClearable={true}
                                            isSearchable={true}
                                            name="color"
                                            options={options}
                                            theme={(theme: any) => ({
                                                ...theme,
                                                borderRadius: 0,
                                                colors: {
                                                    ...theme.colors,
                                                    primary: '#3E6891',
                                                },
                                            })}
                                        />
                                    </Box>
                                </Grid>
                            </Grid>
                            <Grid container>
                                <Grid item xs={12}>
                                    <Box mt={4}>
                                        <GoodFundsTable />
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                    <Box pt={5}>
                        <Container>
                            <Grid container justify="center" spacing={getSpacing()}>
                                <Grid item>
                                    <Button
                                        onClick={() => {
                                            setExportOpen(true);
                                        }}
                                        variant="contained"
                                        size={buttonSize()}
                                        color="primary"
                                    >
                                        Export to MFP
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="contained" size={buttonSize()} color="primary">
                                        Export to CSV
                                    </Button>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                    <Box>
                        <Container>
                            <Grid container justify="center" spacing={getSpacing()}>
                                <Grid item xs={12}>
                                    <Box p={3} className={classes.note}>
                                        Please note that the recommendations above are based on our analysis of
                                        available data and is offered as an indication of funds likely to be suitable
                                        for your needs. The recommendations made on this page do not constitute a
                                        guarantee of returns and are not binding. Please refer to the disclaimer before
                                        using the recommendations.
                                    </Box>
                                </Grid>
                            </Grid>
                        </Container>
                    </Box>
                </Box>
                {openExport && (
                    <ExportCard
                        handleClose={() => {
                            setExportOpen(false);
                        }}
                    />
                )}
            </main>
        </React.Fragment>
    );
}

export default GoodFunds;
